import React, { useContext, useEffect, useRef, useState } from 'react';
import styled from 'styled-components'
import styles from '../constants';
import test1 from '../resources/test1.jpg'
import { mainStore } from '../store/MainStore';
import GridItem from "./GridItem";

const Container = styled.div`
    top: 0px;
    width: 100%;
    flex-shrink: 0;
    display: grid;
    align-content: flex-start;
    justify-content: flex-start;
`


interface propTypes {
    elements:any
    columns: number
    clicked?: any
    customStart?: any
    setPhotoHeight?: any
}
function Grid(props: propTypes) {
    const {state, dispatch} = useContext(mainStore)
    const [photoHeight, setPhotoHeight] = useState(0)

    return (
        <Container
            style={{
                gridTemplateColumns: `repeat(${props.columns}, calc(100% / ${props.columns}) )`,
            }}
        >

            {
                props.customStart &&
                    <GridItem
                        photoHeight={photoHeight}
                    >

                        {props.customStart}
                    </GridItem>
            }
            {
                
                props.elements.map((prop: any, key: number) =>
                    prop &&
                        <GridItem 
                            name={prop.name}
                            type={prop.type}
                            photo={prop.photo}
                            clicked={() => props.clicked(prop)}
                            index={key}
                            key={key}
                            fixHeight={'fixHeight' in prop ? prop.fixHeight : false}
                            noDisplay={'noDisplay' in prop ? prop.noDisplay : false}
                            setPhotoHeight={settingPhotoHeight}
                            photoHeight={photoHeight}
                        />

                )
                
            } 

        </Container>
    );

    function settingPhotoHeight(height:number) {
        setPhotoHeight(height)
        if(props.setPhotoHeight)
            props.setPhotoHeight(height)
    }
}

export default Grid;