import React, { useContext, useLayoutEffect, useRef, useState } from 'react';
import styled from 'styled-components'
import styles from '../constants';
import test1 from '../resources/test1.jpg'
import { mainStore } from '../store/MainStore';
import { SuspenseImg } from "../SuspenseImage";


const GridItemCover = styled.div`
    z-index: 2;
    opacity: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    transition: all 0.3s ease;
    cursor: pointer;
    display: flex;
    flex-grow: 0;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &:after {
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0px;
        left: 0px;
        background: ${styles.red};
        opacity: 0.7;
        z-index: 2;
    }
`

const Item = styled.div`
    position: relative;
    width: 100%;
    justify-content: center;
    align-items: flex-start;
    flex-shrink: 0;
    overflow: hidden;
    &:hover ${GridItemCover} {
        opacity: 1;
    }
`



const CoverHeading = styled.div`
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: flex-end;
    height: calc(50% - 11.5px);
    margin-bottom: 10px;
    font-size: 23px;
    font-weight: bold;
    color: white;
    z-index: 3;
`

const CoverContent = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: calc(50% - 11.5px);
    margin-top: 10px;
    font-size: 18px;
    font-weight: bold;
    color: white;
    z-index: 3;
    text-align: center;
`


const GridPhotoContainer = styled.div`
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-grow: 0;
    overflow: hidden;
    justify-content: center;
    align-items: center;
`

const GridPhoto = styled.img`
    padding: 0px;
    display: inline-block;
`

interface propTypes {
    photo?:string
    clicked?: any
    index?: number
    children?: any
    name?: string
    type?: string
    fixHeight?: boolean
    noDisplay? : boolean
    setPhotoHeight? :any
    photoHeight: number
}
function GridItem(props: propTypes) {
    const [mouseIn, setMouseIn] = useState(false)
    const containerRef = useRef<HTMLDivElement>(null)

    useLayoutEffect(() => {
        if(props.setPhotoHeight) {
            if(props.index === 0) {

                window.addEventListener('resize', resizeItem);
                resizeItem()
            }
        }
    }, [])

    return (
        
            props.photo ?

            <Item 
                ref={containerRef}
                id="itemContainer"
                style={{
                    display: props.noDisplay ? 'none' : 'flex',
                    height: props.photoHeight,
                }}
                onClick={props.clicked ? props.clicked : undefined}
                onMouseEnter={() => setMouseIn(true)}
                onMouseLeave={() => setMouseIn(false)}
            >
                
                        <GridItemCover
                            style={{}}
                        >
                            <CoverHeading>Wicked Problem</CoverHeading>
                            <div style={{width: 'calc(100% - 50px)', zIndex: 3, height: '3px', background: 'white',}}></div>
                            <CoverContent>
                                {props.name}
                                <div style={{fontWeight: 'normal', fontSize: '15px', marginTop: '5px'}}>
                                    {
                                        props.type === 'jewelry' ?
                                            "JD"
                                            :
                                            props.type === 'fine' ?
                                                "FD"
                                                :
                                                props.type === 'visual' ?
                                                "VCD"
                                                :
                                                ''
                                    }
                                </div>
                            </CoverContent>

                        </GridItemCover>                    
                        <GridPhotoContainer>
                            <GridPhoto 

                                crossOrigin="" 
                                
                                style={{
                                    width: props.fixHeight ? 'auto' : '100%',
                                    height: props.fixHeight ? '100%' : 'auto',
                                }}
                                src={props.photo} 
                            />
                        </GridPhotoContainer>
                                    
            </Item>
            :
            <Item 
                id="itemContainer"
                ref={containerRef}
                style={{
                    gridColumnStart: 1,
                    gridColumnEnd: 3,
                    height: props.photoHeight,
                }}
            >
                {
                    props.children
                }
            </Item>

        
               
    );

    function resizeItem() {
        const element = document.getElementById("itemContainer");
        if(element) {
            console.log(element.clientWidth)
            props.setPhotoHeight(element.clientWidth)
        }
    }
}

export default GridItem;