import React, { useEffect, useRef, useState, useContext } from 'react';
import styled from 'styled-components'
import styles from '../constants';
import { mainStore } from '../store/MainStore';
import Grid from './Grid';

const Container = styled.div`
    height: 100%;
    top: 0px;
    width: 100%;
    top: 0px;
    background: ${styles.background};
    overflow-y: auto;
    flex-shrink: 0;
`

const Heading = styled.div`
    height: 250px;
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    letter-spacing:5px;
    font-weight: bold;
    color: ${styles.black}
`

const GridStart = styled.div`
    height: calc(100% - 20px);
    width: calc(100% - 20px);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    letter-spacing:3px;
    font-weight: bold;
    color: ${styles.black}
`

const GridStartIcon = styled.div`
    height: 100%;
    width: 20%;
`

const GridStartText = styled.div`
    height: 100%;
    width: calc(80% - 30px);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
`


interface propTypes {

}
function JewelryArtists(props: propTypes) {
    const {state, dispatch} = useContext(mainStore)
    const [artists, setArtists] = useState<any[]>([])
    useEffect(() => {
        let newArtists:any[] = []
        state.allArtists.forEach((prop:any) => { 
            if(prop.type==="jewelry")
                newArtists.push(prop)
        })

        setArtists(newArtists)
        console.log(newArtists)
    },[state.allArtists])
    
    return (
        <Container
            style={{
    
            }}
        >
            <Heading
                style={{
                    fontSize: state.mobileOpen ? '50px': '80px'
                }}
            >
                Jewelry Design
            </Heading>
           
            <Grid
                customStart={
                    <GridStart>
                        <GridStartIcon>
                        </GridStartIcon>

                        <GridStartText
                            style={{
                                fontSize: state.mobileOpen ? '15px' : '20px',
                            }}
                        >
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et
                        </GridStartText>
                    </GridStart>
                }
                columns={state.mobileOpen ? 2 : 4}
                elements={artists}
                clicked={artistClicked}
            />
           

        </Container>
    );

    function artistClicked(artist:any) {
        dispatch({
            type: 'setArtist',
            artistOpen: artist.name,
            artistType: state.artistType
        })
    }

}

export default JewelryArtists;