import React, { useContext, useEffect, useRef, useState } from 'react';
import styled from 'styled-components'
import { mainStore, MainStateProvider } from '../store/MainStore'
import Grid from '../components/Grid';

import test1 from '../resources/test1.jpg'
import test2 from '../resources/test2.jpg'
import test3 from '../resources/test3.jpg'
import test4 from '../resources/test4.jpg'
import test5 from '../resources/test5.jpg'
import test6 from '../resources/test6.jpg'
import test7 from '../resources/test7.jpg'
import test8 from '../resources/test8.jpg'
import styles from '../constants';

const Container = styled.div`
    width: 100%;
    scroll-snap-align: center;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-size: 30px;
`

const Selector = styled.div`
    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
`

const InfoContainerContainer = styled.div`
    height: 100%;
    display: flex;
    justify-content: flex-start;
    overflow: hidden;
    align-items: flex-start;
    transition: all 0.3s ease;
`

const SelectorButton = styled.div`
    cursor: pointer;
    display: flex;
    justify-content: center;
    font-weight: bold;
    font-size: 18px;
`

const ViewAllContainer = styled.div`
    display: flex;
    height: 50px;
    justify-content: center;
    align-items: center;
`

const ViewAllButton = styled.div`
    padding: 0px 10px;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
`

interface propTypes {
    setArtist: any
}
function AboutPage(props: propTypes) {
    const {state, dispatch} = useContext(mainStore)
    const [selected, setSelected] = useState("all")
    const [selectedPhotos, setSelectedPhotos] = useState<any[]>([])
    const [photoHeight, setPhotoHeight] = useState<number>(0)
    const [visibleItems, setVisibleItems] = useState<number>(0)
    const [viewAll, setViewAll] = useState<boolean>(false)

    useEffect(() => {
        selectPictures('all')
    }, [])

    return (
        <Container
            style={{
                height: 'auto'
            }}
        >
            <Selector>
                <SelectorButton
                    style={{
                        color: selected === 'all' ? styles.pink : styles.black,
                        width: state.mobileOpen ? '25%' : '10%',
                    }}
                    onClick={() => selectPictures('all')}
                >
                    ALL
                </SelectorButton>

                <SelectorButton
                    style={{
                        color: selected === 'jewelry' ? styles.pink : styles.black,
                        width: state.mobileOpen ? '25%' : '10%',
                    }}
                    onClick={() => selectPictures('jewelry')}
                >
                    JD
                </SelectorButton>

                <SelectorButton
                    style={{
                        color: selected === 'fine' ? styles.pink : styles.black,
                        width: state.mobileOpen ? '25%' : '10%',
                    }}
                    onClick={() => selectPictures('fine')}
                >
                    FA
                </SelectorButton>

                <SelectorButton
                    style={{
                        color: selected === 'visual' ? styles.pink : styles.black,
                        width: state.mobileOpen ? '25%' : '10%',
                    }}
                    onClick={() => selectPictures('visual')}
                >
                    VCD
                </SelectorButton>
            </Selector>

            
            <InfoContainerContainer
                style={{
                    position: 'relative',
                    width: '100%',
                    maxHeight: viewAll ? (state.mobileOpen ? photoHeight * Math.ceil(visibleItems/2) : photoHeight * Math.ceil(visibleItems/4) ) : (state.mobileOpen ? photoHeight * 4 : photoHeight * 2)
                }}
            >
                <Grid 
                    columns={state.mobileOpen ? 2 : 4}
                    clicked={gridClicked}
                    elements={selectedPhotos}
                    setPhotoHeight={setPhotoHeight}
                />
            </InfoContainerContainer>

            <ViewAllContainer>
                <ViewAllButton onClick={() => setViewAll(!viewAll)}>{viewAll ? "LESS" : "MORE"}</ViewAllButton>
            </ViewAllContainer>
            
        </Container>
    );

    function selectPictures(sel:string) {
        let newSelectedPhotos:any[] = []

        let blank:any[] = []
        let newPreview:any[] = blank.concat(state.previewPhotos)
        console.log(state.previewPhotos)
        newPreview.forEach((prop:any) => { 
            let newProp = Object.assign({}, prop)
            newProp["noDisplay"] = false
            if(sel === "all")
                newSelectedPhotos.push(newProp) 
            else if(sel === newProp.type)  {
                newSelectedPhotos.push(newProp)
            }
            else {
                newProp.noDisplay = true
                newSelectedPhotos.push(newProp) 
            }

        })

        setSelected(sel)
        setSelectedPhotos(newSelectedPhotos)

        let visible = 0
        newSelectedPhotos.forEach((prop:any, index:number) => {
            if(!prop.noDisplay)
                visible++
        })

        setVisibleItems(visible)
    }

    function gridClicked(key: any) {
        props.setArtist('Allingham Emily', '')
    }
}

export default AboutPage;